import infoBox from "../components/boxstyle/infobox.vue"
export default {
	data() {
		return {
			statusNum: 0,
			id: null,
			// orderNo: '4',
			orderStatus: '',
			// 获取经纬度
			longitude: '',
			latitude: ""
		}
	},

	methods: {
		// 页面跳转部分
		toApply() {
			this.getStatus()
			if (this.orderStatus !== '') {
				this.infoToShow()
			}
			this.orderStatus === '' &&
				uni.navigateTo({
					url: "/pages/home/weight/apply?id=" + this.id
				})
		},
		toWeight() {
			this.getStatus()
			if (this.orderStatus !== 1) {
				this.infoToShow()
			}
			this.orderStatus === 1 &&
				uni.navigateTo({
					url: "/pages/home/weight/weight?id=" + this.id
				})
		},
		toDone() {
			this.getStatus()
			if (this.orderStatus !== 2) {
				this.infoToShow()
			}
			this.orderStatus === 2 &&
				uni.navigateTo({
					url: "/pages/home/image/doneimg?id=" + this.id
				})
		},
		toDownSign() {
			this.getStatus()
			if (this.orderStatus !== 3) {
				this.infoToShow()
			}
			this.orderStatus === 3 &&
				uni.navigateTo({
					url: "/pages/home/image/upload?id=" + this.id
				})
		},
		toUpload() {
			this.getStatus()
			if (this.orderStatus !== 4) {
				this.infoToShow()
			}
			this.orderStatus === 4 &&
				uni.navigateTo({
					url: "/pages/home/image/signupload?id=" + this.id
				})
		},
		// 获取当前订单状态
		async getStatus() {
			const opts = {
				url: "/personal/driver/getOrderRecord",
				method: "post"
			}

			const res = await this.$http.httpTokenRequest(opts);
			console.log(res)

			if (res.data.code === 2) {
				uni.showToast({
					title: res.data.msg,
					icon: "none"
				})
				setTimeout(() => {
					uni.reLaunch({
						url: "/pages/login/status"
					})
				}, 800)

			}
			if (res.data.code === 1) {
				this.orderStatus = ''
			} else {
				this.orderStatus = res.data.result.orderStatus;
			}
			this.id = res.data.result.id;
		},

		// 提示内容
		infoToShow() {
			let content = '';
			if (this.orderStatus === "") content = "请填写运单申请页面";
			if (this.orderStatus === 1) content = "请填写物料重量页面";
			if (this.orderStatus === 2) content = "请填写到货打卡页面";
			if (this.orderStatus === 3) content = "请填写开始卸货打卡页面";
			if (this.orderStatus === 4) content = "请填写上传签收单页面";

			uni.showToast({
				title: content,
				icon: "none"
			})
		},

		// 获取当前的身份状态
		statusOfMan() {
			const status = uni.getStorageSync("USER_STATUS");
			console.log(status);
			if (status === "CONSIGNEE") {
				uni.reLaunch({
					url: "/pages/user/user"
				})
				uni.showToast({
					title: "请切换至其它身份",
					icon: "none"
				})
			}
		},
		// 上传身份信息
		async subUserInfo() {
			const info = this.longitude + "," + this.latitude;
			console.log(info);
			const opts = {
				url: "/personal/driver/addPoints",
				method: "post"
			}
			const params = {
				location: info
			}
			console.log(params)
			const res = await this.$http.httpTokenRequest(opts, params);
			console.log(res);

		},
		// 经纬度
		getLocation() {
			var that = this;
			uni.getLocation({
				type: 'wgs84',
				success: function(res) {
					console.log('当前位置的经度：' + res.longitude)
					that.longitude = res.longitude;
					console.log('当前位置的纬度：' + res.latitude);
					that.latitude = res.latitude;
				}
			})
		}
	},

	components: {
		infoBox
	},

	onShow() {
		this.getStatus();
		this.getLocation();
	},

	onLoad() {
		this.statusOfMan();
		this.subUserInfo();
		this.getStatus();
		setTimeout(() => {
			this.subUserInfo();
		}, 2000)
	},
}
